.event-item {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0.5rem;
  margin: 1rem 0;
  /* background-color: rgb(31, 31, 31); */
  font-size: 2rem;
  background: rgba(32, 31, 31, 0.815);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(9.5px);
  -webkit-backdrop-filter: blur(9.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  height: 100px;
}

.event-item__description {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1;
  border-radius: 12px;
}

.event-item__type {
  padding: 0.5rem;
  font-size: 2rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
}

.delete-button {
  background-color: #f44336;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 12px;
}

.notsignup-check {
  color: red;
}

.notsignup-check:hover {
  color: #f07070;
}

.event-signupCheck {
  font-size: 2rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
  text-align: right;
}

.signup-check:hover {
  color: #97FB57;
}

.event-signupCheck::after {
  content: attr(title);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  padding: 5px;
  background-color: #333;
  color: #fff;
  font-size: 12px;
  border-radius: 3px;
  white-space: nowrap;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.3s ease-in-out;
}

.notsignup-check::after {
  content: attr(title);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  padding: 5px;
  background-color: #333;
  color: #fff;
  font-size: 12px;
  border-radius: 3px;
  white-space: nowrap;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.3s ease-in-out;
}

/* .event-signupCheck:hover::after {
  visibility: visible;
  opacity: 1;
} */

.signup-check {
  color: green;
}

.event-deleteitem {
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
  text-align: right;
}

.event-notsignupcheck {
  font-size: 2rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
  text-align: right;
}

.event-item__event {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: green;
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 12px;
}

.event-item__description {
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  flex: 1;
  font-size: 1.25rem;
}

.event-item__event {
  font-size: 1.25rem;
  padding: 0.5rem 1.5rem;
}

@media (max-width: 599px) {
  .event-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
    font-size: 1.25rem;
  }

  .event-item__event {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}
